import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

@Component({
  tag: 'checkbox-input',
  styleUrl: 'checkbox-input.css',
  shadow: true,
})
export class CheckboxInput {
  /**
   * The name of the checkbox input.
   */
  @Prop() name: string = '';

  /**
   * The value of the checkbox.
   */
  @Prop() value: string = '';

  /**
   * The label to display next to the checkbox.
   */
  @Prop() label: string= '';

  /**
   * Whether the checkbox is currently checked.
   */
  @Prop({ mutable: true, reflect: true }) checked: boolean = false;

  /**
   * Whether the checkbox is disabled.
   */
  @Prop() disabled: boolean = false;

  /**
   * Event emitted when the checkbox's checked state changes.
   */
  @Event() myChange: EventEmitter<{ value: string; checked: boolean }> | undefined;

  private handleChange = (event: Event) => {
    if (this.disabled) {
      return;
    }
    const input = event.target as HTMLInputElement;
    this.checked = input.checked;
    this.myChange?.emit({ value: this.value, checked: this.checked });
  };

  render() {
    return (
      <label class={{ 'checkbox-wrapper': true, 'checkbox-wrapper--disabled': this.disabled }}>
        <input
          type="checkbox"
          name={this.name}
          value={this.value}
          checked={this.checked}
          disabled={this.disabled}
          onChange={this.handleChange}
          class="checkbox-input"
        />
        <span class="checkbox-custom">
          <svg class="checkbox-checkmark" viewBox="0 0 24 24">
            <polyline points="20 6 9 17 4 12"></polyline>
          </svg>
        </span>
        <span class="checkbox-label">{this.label}</span>
      </label>
    );
  }
}
